export default {
	data() {
		return {
			f: [],
			g: [],
			d: [],
			s: [],
		}
	},
	onLoad: function() {
		uni.request({
			url: 'http://' + getApp().globalData.urlStr + '/GetTop',
			method: 'GET',
			data: {},
			success: res => {
				this.f = res.data.data.f;
				this.g = res.data.data.g;
				this.d = res.data.data.d;
				this.s = res.data.data.s;
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods:{
		getImgUrl(icon){
			return require("../../static/img/top"+icon+".png");
		}
	}
}
